import React, { Component } from 'react';
import RaisedButton from 'material-ui/RaisedButton';
import { deepOrange500 } from 'material-ui/styles/colors';
import FlatButton from 'material-ui/FlatButton';
import { Card, CardActions, CardHeader, CardText } from 'material-ui/Card';
import TextField from 'material-ui/TextField';
import ContentAdd from 'material-ui/svg-icons/content/add';
import getMuiTheme from 'material-ui/styles/getMuiTheme';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';

import { Link, Redirect } from 'react-router-dom';


const muiTheme = getMuiTheme({
    palette: {
        accent1Color: deepOrange500,
    },
});

const styles = {
    title: {
        textAlign: 'center',
        paddingTop: 20,
    },
    post: {
        paddingTop: 50,
    },
    postbutton: {
        position: 'fixed',
        right: '50px',
        bottom: '90px',
    }
};

const apiurl = 'https://reactblog.now.sh/api/post';
const apiurlnow = 'https://comment-box.now.sh/api/comments';

class Post extends Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
            data: [],
            title: 'My first post',
            author: 'Anonymous',
            text: '',
            Redirect: false
        };
    }

    handleTouchTapGET = () => {
        fetch(apiurl, { method: 'GET' })
            .then((res) => res.json())
            .then((j) => { this.setState({ data: j }); })
            .catch((err) => (console.log(err)))
    }

    handleTouchTapPOST = (e) => {
        fetch(apiurl, {
            method: 'POST', headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }, body: JSON.stringify({ title: this.state.title, author: this.state.author, text: this.state.text })
        })
            .then((res) => res.json())
            .then((j) => console.log(j))
            .catch((err) => console.log(err))

        fetch(apiurl, { method: 'GET' })
            .then((res) => res.json())
            .then((j) => { this.setState({ data: j }); })
            .catch((err) => console.log(err))

        this.setState({ text: '', Redirect: true });
    }
    authoronChange = (e) => {
        this.setState({ author: e.target.value });
    }
    textonChange = (e) => {
        // if(e.keyCode == 13) e.target.value += '<br>';
        this.setState({ text: e.target.value });
    }
    titleonChange = (e) => {
        this.setState({ title: e.target.value });
    }

    render() {
        if (this.state.Redirect) { return <Redirect push to="/" />; }
        return (
            <MuiThemeProvider muiTheme={muiTheme}>
                <div>
                    <h1 style={styles.title}>Create A New Blog Post!</h1>
                    <div style={styles.post}>
                        <div>Title:</div>
                        <TextField
                            defaultValue="My first post"
                            onChange={this.titleonChange}
                        />
                        <div>Your Name:</div>
                        <TextField
                            defaultValue=''
                            onChange={this.authoronChange}
                        />
                        <div>Post</div>
                        <TextField
                            hintText="type here"
                            value={this.state.text}
                            fullWidth={true}
                            onChange={this.textonChange}
                            multiLine={true}
                            rows={8}
                        />
                        <div>
                            <RaisedButton
                                style={styles.postbutton}
                                label="POST"
                                secondary={true}
                                onTouchTap={this.handleTouchTapPOST}
                            />
                        </div>
                    </div>
                    <h2><Link to='/'>back</Link></h2>
                </div>
            </MuiThemeProvider>
        );
    }
}
export default Post;



