import React, { Component } from 'react';
import Avatar from 'material-ui/Avatar';
import { Card, CardActions, CardHeader, CardText, CardTitle } from 'material-ui/Card';
import getMuiTheme from 'material-ui/styles/getMuiTheme';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import { deepOrange500 } from 'material-ui/styles/colors';
import FloatingActionButton from 'material-ui/FloatingActionButton';
import Reply from 'material-ui/svg-icons/content/reply';

import { Link, Redirect } from 'react-router-dom';

const muiTheme = getMuiTheme({
  palette: {
    accent1Color: deepOrange500,
  },
});

const styles = {
  backbutton: {
    position: 'fixed',
    right: '50px',
    bottom: '50px',
  },
  whitespace: {
    whitespace: 'pre-wrap',
  },
};


const apiurl = 'https://reactblog.now.sh/api/posts/';

class Ablog extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: {},
      Redirect: false
    };
  }
  handleGET = (id) => {
    fetch(apiurl + id, { method: 'GET' })
      .then((res) => res.json())
      .then((j) => { this.setState({ data: j }); })
      .catch((err) => console.log(err))
  }
  componentDidMount() {
    this.handleGET(this.props.match.params.id);
  }
  handleRedirectback = () => {
    this.setState({ Redirect: true });
  }

  render() {
    if (this.state.Redirect) { return <Redirect push to="/" />; }
    return (
      <MuiThemeProvider muiTheme={muiTheme}>
        <div>
          <Card>
            <CardHeader
              title={this.state.data.author}
              subtitle={this.state.data.time}
            />
            <CardTitle title={this.state.data.title} />
            <CardText><pre>{this.state.data.text}</pre></CardText>
          </Card>
          <FloatingActionButton style={styles.backbutton} onTouchTap={this.handleRedirectback}>
            <Reply />
          </FloatingActionButton>
        </div>
      </MuiThemeProvider>
    );
  }
}
export default Ablog;



