
import React, { Component } from 'react';
import Ablog from './Ablog';
import Blogpreview from './Blogpreview';
import RaisedButton from 'material-ui/RaisedButton';
import { deepOrange500 } from 'material-ui/styles/colors';
import FlatButton from 'material-ui/FlatButton';
import { Card, CardActions, CardHeader, CardText } from 'material-ui/Card';
import FloatingActionButton from 'material-ui/FloatingActionButton';
import ContentAdd from 'material-ui/svg-icons/content/add';
import getMuiTheme from 'material-ui/styles/getMuiTheme';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';

import { Link, Redirect } from 'react-router-dom';

const muiTheme = getMuiTheme({
  palette: {
    accent1Color: deepOrange500,
  },
});

const styles = {
  title: {
    textAlign: 'center',
    paddingTop: 20,
  },
  post: {
    paddingTop: 50,
    paddingLeft: 550,
  },
  postbutton: {
    position: 'fixed',
    right: '50px',
    bottom: '50px',
  }
};

const apiurl = 'https://reactblog.now.sh/api/posts';
const apiurlnow = 'https://comment-box.now.sh/api/comments';
var timerid;

class Main extends Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
      data: [],
      author: 'Anonymous',
      Redirect: false
    };
  }


  handleTouchTapGET = () => {
    fetch(apiurl, { method: 'GET' })
      .then((res) => res.json())
      .then((j) => { this.setState({ data: j }); })
      .catch((err) => (console.log(err)))
  }

  componentDidMount() {
    this.handleTouchTapGET();
    timerid = setInterval(() => this.handleTouchTapGET(), 10000);
  }
  componentWillUnmount() {
    clearInterval(timerid);
  }
  handleRedirectpost = () => {
    this.setState({ Redirect: true });
  }

  render() {
    const blogNode = this.state.data.map((item, index) => {
      return (<Blogpreview id={item._id} author={item.author} title={item.title} time={item.time} />);
    });
    if (this.state.Redirect) { return <Redirect push to="/post" />; }
    return (
      <MuiThemeProvider muiTheme={muiTheme}>
        <div>
          <h1 style={styles.title}>My Blog</h1>
          <div>{blogNode}</div>
          <FloatingActionButton style={styles.postbutton} onTouchTap={this.handleRedirectpost}>
            <ContentAdd />
          </FloatingActionButton>
        </div>
      </MuiThemeProvider>
    );
  }
}

export default Main;



